<template lang="pug">
  .container.rows.wrapRWD
    .card(v-for="(type, index) in typeMapping", v-scroll-reveal.reset={origin: 'right', distance: '100px', delay: 200, reset: true})
      font-awesome-icon(v-if="index === 'finance'", icon="file-invoice-dollar", size="3x")
      font-awesome-icon(v-if="index === 'regular'", icon="file-alt", size="3x")
      h3 {{ type }}
      ul.rows
        li.item(v-for="report in getLastReports(index)")
          a(:href="report.url")
            p {{ report.year }}<br/>
            h3 Q{{ report.quater }}
</template>

<script>
import VueScrollReveal from 'vue-scroll-reveal'
import Datas from '@/../static/json/reports.json'

export default {
  name: 'HomeCards',
  data () {
    return {
      typeMapping: {
        finance: '最新財報',
        regular: '最新季報'
      },
      reports: Datas
    }
  },
  components: {
    VueScrollReveal
  },
  methods: {
    getLastReports (type) {
      const sametype = this.reports.filter(s => s.type === type)
      return sametype.slice(0, 3)
    }
  }
}
</script>

<style lang="sass" scopped>

.card
  +fs($c: $c-black, $ta: center)
  padding: 20px
  border-radius: 2px
  background-color: #fff
  border: 1px $c-primary solid
  width: 0
  flex: 1 1 auto
  margin: 20px 10px
  svg
    color: $c-primary
    padding: 10px 0 20px
  h3
    padding: 10px 0

.item
  +fs($c: $c-smoke, $fz: 24px, $fw: 400)
  width: 0
  flex: 1 1 auto
  margin: 10px 10px
  border-radius: 2px
  padding: 12px
  h3
    +fs($c: $c-smoke, $fw: 700)
</style>



